export class Observer {
    constructor() {
        this.eventListeners = new Map();
    }
    /**
     * 监听事件
     * @param channel
     * @param handler
     */
    on(channel, handler) {
        if (this.eventListeners.has(channel)) {
            const listeners = this.eventListeners.get(channel);
            listeners.add(handler);
        }
        else {
            this.eventListeners.set(channel, new Set([handler]));
        }
        return this;
    }
    /**
     * 发送事件
     * @param channel
     * @param data
     */
    emit(channel, data) {
        if (this.eventListeners.has(channel)) {
            const listeners = this.eventListeners.get(channel);
            for (const listener of listeners) {
                listener(data);
            }
        }
        return this;
    }
    /**
     * 为给定事件添加一次性监听器。
     * @param channel 频道
     * @param listener 事件回调
     * @returns
     */
    once(channel, listener) {
        this.on(channel, (ev) => {
            this.clear(channel, listener);
            listener.call(this, ev);
        });
        return this;
    }
    /**
     * 删除监听的事件
     * @param channel
     * @param handler
     * @returns
     */
    clear(channel, handler) {
        const handlers = this.eventListeners.get(channel);
        if (handlers) {
            return handlers.delete(handler);
        }
        return false;
    }
}
export class MainEventEmitter extends Observer {
    constructor(url) {
        super();
        this.workerListeners = new Map();
        this.worker = new Worker(url);
        this.worker.onmessage = (...args) => {
            this.listenOnmessage(...args);
        };
    }
    /**
     * 发送事件到Worker线程
     * @param channel
     * @param data
     * @param args
     */
    postMessage(channel, data, ...args) {
        this.worker.postMessage({ channel, data }, ...args);
    }
    /**
     * 为给定的Worker线程事件添加一次性侦听器。
     * @param channel 频道
     * @param handler 事件回调
     * @returns
     */
    onmessageOnce(channel, handler) {
        this.onmessage(channel, (ev) => {
            this.clearOnmessage(channel, handler);
            handler.call(this, ev);
        });
        return this;
    }
    /**
     * 清除Worker线程事件
     * @param channel
     * @param handler
     * @returns
     */
    clearOnmessage(channel, handler) {
        const handlers = this.workerListeners.get(channel);
        if (handlers) {
            return handlers.delete(handler);
        }
        return false;
    }
    /**
     * 监听Worker线程发送的事件
     * @param channel
     * @param handler
     */
    onmessage(channel, handler) {
        if (this.workerListeners.has(channel)) {
            const listeners = this.workerListeners.get(channel);
            listeners.add(handler);
        }
        else {
            this.workerListeners.set(channel, new Set([handler]));
        }
    }
    listenOnmessage(ev) {
        const { channel, data } = ev.data;
        if (this.workerListeners.has(channel)) {
            const listeners = this.workerListeners.get(channel);
            for (const listener of listeners) {
                listener(data);
            }
        }
    }
}
export class WorkerEventEmitter {
    constructor() {
        this.eventListeners = new Map();
        onmessage = (...args) => {
            this.listen(...args);
        };
    }
    send(channel, data, ...args) {
        postMessage({ channel, data }, ...args);
    }
    on(channel, handler) {
        if (this.eventListeners.has(channel)) {
            const listeners = this.eventListeners.get(channel);
            listeners.add(handler);
        }
        else {
            this.eventListeners.set(channel, new Set([handler]));
        }
    }
    /**
     * 为给定事件添加一次性侦听器。
     * @param channel 频道
     * @param listener 事件回调
     * @returns
     */
    once(channel, listener) {
        this.on(channel, (ev) => {
            this.clear(channel, listener);
            listener.call(this, ev);
        });
        return this;
    }
    clear(channel, handler) {
        const handlers = this.eventListeners.get(channel);
        if (handlers) {
            return handlers.delete(handler);
        }
        return false;
    }
    listen(ev) {
        const { channel, data } = ev.data;
        if (this.eventListeners.has(channel)) {
            const listeners = this.eventListeners.get(channel);
            for (const listener of listeners) {
                listener(data);
            }
        }
    }
}
