export default class WorkerManager {
    constructor() {
        this.workers = new Map();
    }
    add(id, worker) {
        if (this.has(id))
            return;
        return this.workers.set(id, worker);
    }
    get(id) {
        return this.workers.get(id);
    }
    has(id) {
        return this.workers.has(id);
    }
}
